import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import { auth } from './firebase';
import { onAuthStateChanged } from 'firebase/auth';
import { SocketProvider } from './SocketContext';

import Register from './Register';
import Login from './Login';
import Profile from './Profile';
import Community from './Community';
import AskQuestion from './AskQuestion';
import QuestionDetail from './QuestionDetail';
import ChatPage from './ChatPage';
import FriendProfile from './FriendProfile';
import StudyRoom from './StudyRoom';

import './App.css';

function App() {
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, (currentUser) => {
      if (currentUser) {
        setUser(currentUser);
      } else {
        setUser(null);
      }
      setLoading(false);
    });

    return () => unsubscribe();
  }, []);

  if (loading) {
    return <div>Loading...</div>;
  }

  return (
    <SocketProvider>
      <Router>
        <Routes>
          <Route path="/" element={user ? <Profile /> : <Login />} />
          <Route path="/register" element={<Register />} />
          <Route path="/login" element={<Login />} />
          <Route path="/profile" element={user ? <Profile /> : <Navigate to="/login" />} />
          <Route path="/community" element={user ? <Community /> : <Navigate to="/login" />} />
          <Route path="/ask-question" element={user ? <AskQuestion /> : <Navigate to="/login" />} />
          <Route path="/question/:questionId" element={user ? <QuestionDetail /> : <Navigate to="/login" />} />
          <Route path="/chats" element={user ? <ChatPage /> : <Navigate to="/login" />} />
          <Route 
            path="/friend-profile/:friendId" 
            element={user ? <FriendProfile /> : <Navigate to="/login" />} 
          />
          <Route path="/study-room" element={user ? <StudyRoom /> : <Navigate to="/login" />} />
        </Routes>
      </Router>
    </SocketProvider>
  );
}

export default App;