import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { 
  db, 
  auth, 
  doc, 
  getDoc, 
  updateDoc, 
  arrayUnion, 
  arrayRemove, 
  collection, 
  query, 
  where, 
  getDocs 
} from './firebase';

const FriendSystem = () => {

  const navigate = useNavigate();
  const [friendEmail, setFriendEmail] = useState('');
  const [friendRequests, setFriendRequests] = useState([]);
  const [friends, setFriends] = useState([]);
  const [searchResults, setSearchResults] = useState([]);

  useEffect(() => {
    fetchFriendData();
  }, []);

  const fetchFriendData = async () => {
    const user = auth.currentUser;
    if (!user) return;

    const userDoc = await getDoc(doc(db, 'users', user.uid));
    const userData = userDoc.data();

    setFriendRequests(userData.friendRequests || []);
    setFriends(userData.friends || []);
  };

  const searchUser = async () => {
    if (!friendEmail.trim()) return;

    // Search by email or username
    const usersRef = collection(db, 'users');
    const q = query(
      usersRef, 
      where('email', '==', friendEmail) || 
      where('username', '==', friendEmail)
    );

    const querySnapshot = await getDocs(q);
    const results = querySnapshot.docs
      .filter(d => d.id !== auth.currentUser.uid)
      .map(doc => ({ id: doc.id, ...doc.data() }));

    setSearchResults(results);
  };

  const sendFriendRequest = async (targetUserId) => {
    const currentUser = auth.currentUser;
    const currentUserDoc = await getDoc(doc(db, 'users', currentUser.uid));
    const targetUserDoc = await getDoc(doc(db, 'users', targetUserId));

    const currentUserData = currentUserDoc.data();
    const targetUserData = targetUserDoc.data();

    // Update target user's friend requests
    await updateDoc(doc(db, 'users', targetUserId), {
      friendRequests: arrayUnion({
        userId: currentUser.uid,
        username: currentUserData.username,
        email: currentUser.email
      })
    });

    alert('Friend request sent!');
  };

  const acceptFriendRequest = async (requestUser) => {
    const currentUser = auth.currentUser;

    // Add to current user's friends
    await updateDoc(doc(db, 'users', currentUser.uid), {
      friends: arrayUnion({
        userId: requestUser.userId,
        username: requestUser.username,
        email: requestUser.email
      }),
      friendRequests: arrayRemove(requestUser)
    });

    // Add current user to requester's friends
    await updateDoc(doc(db, 'users', requestUser.userId), {
      friends: arrayUnion({
        userId: currentUser.uid,
        username: auth.currentUser.displayName,
        email: currentUser.email
      })
    });

    fetchFriendData();
  };

  const rejectFriendRequest = async (requestUser) => {
    const currentUser = auth.currentUser;

    await updateDoc(doc(db, 'users', currentUser.uid), {
      friendRequests: arrayRemove(requestUser)
    });

    fetchFriendData();
  };

  const removeFriend = async (friend) => {
    const currentUser = auth.currentUser;

    await updateDoc(doc(db, 'users', currentUser.uid), {
      friends: arrayRemove(friend)
    });

    await updateDoc(doc(db, 'users', friend.userId), {
      friends: arrayRemove({
        userId: currentUser.uid,
        username: auth.currentUser.displayName,
        email: currentUser.email
      })
    });

    fetchFriendData();
  };

  return (
    <div className="p-4">
      <h2 className="text-2xl font-bold mb-4">Friend System</h2>

      {/* Search for Friends */}
      <div className="mb-4 flex">
        <input 
          type="text" 
          placeholder="Search by email or username" 
          value={friendEmail}
          onChange={(e) => setFriendEmail(e.target.value)}
          className="flex-grow p-2 border rounded-l"
        />
        <button 
          onClick={searchUser}
          className="bg-blue-500 text-white px-4 py-2 rounded-r"
        >
          Search
        </button>
      </div>

      {/* Search Results */}
      {searchResults.length > 0 && (
        <div className="mb-4">
          <h3 className="font-bold mb-2">Search Results</h3>
          {searchResults.map(user => (
            <div key={user.id} className="flex justify-between items-center border p-2 mb-2">
              <div>
                <p>{user.username}</p>
                <p className="text-gray-500">{user.email}</p>
              </div>
              <button 
                onClick={() => sendFriendRequest(user.id)}
                className="bg-green-500 text-white px-3 py-1 rounded"
              >
                Send Request
              </button>
            </div>
          ))}
        </div>
      )}

      {/* Friend Requests */}
      <div className="mb-4">
        <h3 className="font-bold mb-2">Friend Requests ({friendRequests.length})</h3>
        {friendRequests.map(request => (
          <div key={request.userId} className="flex justify-between items-center border p-2 mb-2">
            <div>
              <p>{request.username}</p>
              <p className="text-gray-500">{request.email}</p>
            </div>
            <div>
              <button 
                onClick={() => acceptFriendRequest(request)}
                className="bg-green-500 text-white px-3 py-1 rounded mr-2"
              >
                Accept
              </button>
              <button 
                onClick={() => rejectFriendRequest(request)}
                className="bg-red-500 text-white px-3 py-1 rounded"
              >
                Reject
              </button>
            </div>
          </div>
        ))}
      </div>

      {/* Friends List */}
      <div>
        <h3 className="font-bold mb-2">Friends ({friends.length})</h3>
        {friends.map(friend => (
          <div key={friend.userId} className="flex justify-between items-center border p-2 mb-2">
            <div>
              <p>{friend.username}</p>
              <p className="text-gray-500">{friend.email}</p>
            </div>
            <div>
              <button 
                onClick={() => removeFriend(friend)}
                className="bg-red-500 text-white px-3 py-1 rounded mr-2"
              >
                Remove
              </button>
              <button 
  onClick={() => navigate(`/friend-profile/${friend.userId}`)}
  className="bg-blue-500 text-white px-3 py-1 rounded"
>
  View Profile
</button>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default FriendSystem;