import React, { useState, useEffect } from "react";
import { useNavigate, useParams } from "react-router-dom";
import { db, doc, getDoc, auth, collection, query, where, getDocs, addDoc } from "./firebase";
import "./FriendProfile.css";

const FriendProfile = () => {
  const { friendId } = useParams();
  const [friendData, setFriendData] = useState(null);
  const [loading, setLoading] = useState(true);
  const [isFriend, setIsFriend] = useState(false);
  const navigate = useNavigate();

  useEffect(() => {
    const fetchFriendProfile = async () => {
      try {
        const friendDoc = await getDoc(doc(db, "users", friendId));
        if (friendDoc.exists()) {
          setFriendData(friendDoc.data());
          
          // Check if this user is already a friend
          const currentUser = auth.currentUser;
          const friendsQuery = query(
            collection(db, 'users', currentUser.uid, 'friends'),
            where('userId', '==', friendId)
          );
          
          const friendsSnapshot = await getDocs(friendsQuery);
          setIsFriend(!friendsSnapshot.empty);
        } else {
          console.log("No friend data found");
        }
        setLoading(false);
      } catch (error) {
        console.error("Error fetching friend profile:", error);
        setLoading(false);
      }
    };

    fetchFriendProfile();
  }, [friendId]);

  const handleAddFriend = async () => {
    const currentUser = auth.currentUser;
    try {
      // Add friend to current user's friends
      await addDoc(collection(db, 'users', currentUser.uid, 'friends'), {
        userId: friendId,
        name: friendData.name,
        username: friendData.username,
        addedAt: new Date()
      });

      // Optionally, add current user to friend's friends (if you want bidirectional)
      await addDoc(collection(db, 'users', friendId, 'friends'), {
        userId: currentUser.uid,
        name: currentUser.displayName,
        username: currentUser.email.split('@')[0],
        addedAt: new Date()
      });

      setIsFriend(true);
      alert('Friend added successfully!');
    } catch (error) {
      console.error("Error adding friend:", error);
    }
  };

  const handleStartChat = () => {
    navigate('/chats', { 
      state: { 
        selectedFriend: {
          id: friendId,
          name: friendData.name,
          username: friendData.username
        } 
      } 
    });
  };

  if (loading) {
    return <div className="loading">Loading friend profile...</div>;
  }

  if (!friendData) {
    return <div className="not-found">Friend profile not found</div>;
  }

  return (
    <div className="friend-profile-container">
      <div className="friend-profile-card">
        <div className="friend-profile-header">
          <div className="friend-profile-info">
            <h2 className="friend-profile-name">{friendData.name}</h2>
            <p className="friend-profile-username">@{friendData.username}</p>
          </div>
        </div>
  
        <div className="friend-profile-details">
          <p><strong>Class:</strong> {friendData.class}</p>
          <p><strong>Age:</strong> {friendData.age}</p>
          <p><strong>Email:</strong> {friendData.email}</p>
        </div>
  
        <div className="friend-profile-buttons">
          {!isFriend && (
            <button 
              onClick={handleAddFriend}
              className="friend-profile-add-btn"
            >
              Add Friend
            </button>
          )}
          <button 
            onClick={handleStartChat}
            className="friend-profile-chat-btn"
            disabled={!isFriend}
          >
            {isFriend ? 'Start Chat' : 'Add Friend to Chat'}
          </button>
          <a href="/profile" className="friend-profile-back-btn">Back</a>
        </div>
      </div>
    </div>
  );  
};

export default FriendProfile;