import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { 
  db, 
  getDoc,
  doc,
  auth, 
  collection, 
  addDoc 
} from './firebase';

const AskQuestion = () => {
  const [questionData, setQuestionData] = useState({
    text: '',
    subject: '',
    tags: []
  });
  const navigate = useNavigate();

  const handleSubmit = async (e) => {
    e.preventDefault();
    const user = auth.currentUser;

    try {
      const userDoc = await getDoc(doc(db, 'users', user.uid));
      const userData = userDoc.data();

      await addDoc(collection(db, 'community_questions'), {
        text: questionData.text,
        subject: questionData.subject,
        tags: questionData.tags,
        author: user.uid,
        authorName: user.displayName || user.email.split('@')[0],
        class: userData.class,
        timestamp: new Date(),
        replyCount: 0
      });

      navigate('/community');
    } catch (error) {
      console.error("Error posting question:", error);
      alert("Failed to post question");
    }
  };

  return (
    <div className="container mx-auto p-4">
      <h1 className="text-2xl font-bold mb-6">Ask a Question</h1>
      
      <form onSubmit={handleSubmit} className="max-w-md mx-auto">
        <div className="mb-4">
          <label className="block mb-2">Subject</label>
          <select 
            value={questionData.subject}
            onChange={(e) => setQuestionData({...questionData, subject: e.target.value})}
            className="w-full border p-2 rounded"
            required
          >
            <option value="">Select Subject</option>
            <option value="Math">Math</option>
            <option value="Science">Science</option>
            <option value="History">History</option>
            <option value="English">English</option>
          </select>
        </div>

        <div className="mb-4">
          <label className="block mb-2">Question</label>
          <textarea 
            value={questionData.text}
            onChange={(e) => setQuestionData({...questionData, text: e.target.value})}
            className="w-full border p-2 rounded h-40"
            placeholder="Type your question here..."
            required
          />
        </div>

        <div className="mb-4">
          <label className="block mb-2">Tags (Optional)</label>
          <input 
            type="text"
            value={questionData.tags.join(', ')}
            onChange={(e) => setQuestionData({...questionData, tags: e.target.value.split(', ')})}
            className="w-full border p-2 rounded"
            placeholder="Enter tags separated by commas"
          />
        </div>

        <div className="flex justify-between">
          <button 
            type="button"
            onClick={() => navigate('/community')}
            className="bg-gray-500 text-white px-4 py-2 rounded"
          >
            Cancel
          </button>
          <button 
            type="submit"
            className="bg-blue-500 text-white px-4 py-2 rounded"
          >
            Post Question
          </button>
        </div>
      </form>
    </div>
  );
};

export default AskQuestion;