import React, { useState, useEffect } from 'react';
import { auth, db } from './firebase';
import { collection, addDoc, getDocs, query, where } from 'firebase/firestore';

const StudyRooms = () => {
  const [studyRooms, setStudyRooms] = useState([]);
  const [newRoomName, setNewRoomName] = useState('');
  const [selectedSubject, setSelectedSubject] = useState('');
  const [selectedClass, setSelectedClass] = useState('');

  const subjects = ['Mathematics', 'Physics', 'Chemistry', 'Biology', 'Computer Science'];
  const classes = ['9', '10'];

  useEffect(() => {
    fetchStudyRooms();
  }, []);

  const fetchStudyRooms = async () => {
    const roomsQuery = query(
      collection(db, 'study_rooms'),
      where('class', '==', auth.currentUser.class)
    );
    const querySnapshot = await getDocs(roomsQuery);
    const rooms = querySnapshot.docs.map(doc => ({
      id: doc.id,
      ...doc.data()
    }));
    setStudyRooms(rooms);
  };

  const createStudyRoom = async () => {
    if (!newRoomName || !selectedSubject || !selectedClass) {
      alert('Please fill in all fields');
      return;
    }

    const newRoom = {
      name: newRoomName,
      subject: selectedSubject,
      class: selectedClass,
      creator: auth.currentUser.uid,
      creatorName: auth.currentUser.displayName,
      createdAt: new Date(),
      members: [auth.currentUser.uid]
    };

    try {
      const docRef = await addDoc(collection(db, 'study_rooms'), newRoom);
      setStudyRooms([...studyRooms, { id: docRef.id, ...newRoom }]);
      
      // Reset form
      setNewRoomName('');
      setSelectedSubject('');
      setSelectedClass('');
    } catch (error) {
      console.error('Error creating study room', error);
    }
  };

  return (
    <div className="container mx-auto p-4">
      <h2 className="text-2xl font-bold mb-4">Study Rooms</h2>

      {/* Create Study Room Form */}
      <div className="mb-6 p-4 bg-gray-100 rounded-lg">
        <h3 className="text-xl font-semibold mb-4">Create Study Room</h3>
        <div className="grid grid-cols-2 gap-4">
          <input 
            type="text"
            placeholder="Room Name"
            value={newRoomName}
            onChange={(e) => setNewRoomName(e.target.value)}
            className="p-2 border rounded"
          />
          <select 
            value={selectedSubject}
            onChange={(e) => setSelectedSubject(e.target.value)}
            className="p-2 border rounded"
          >
            <option value="">Select Subject</option>
            {subjects.map(subject => (
              <option key={subject} value={subject}>{subject}</option>
            ))}
          </select>
          <select 
            value={selectedClass}
            onChange={(e) => setSelectedClass(e.target.value)}
            className="p-2 border rounded"
          >
            <option value="">Select Class</option>
            {classes.map(classLevel => (
              <option key={classLevel} value={classLevel}>{classLevel}</option>
            ))}
          </select>
          <button 
            onClick={createStudyRoom}
            className="bg-blue-500 text-white p-2 rounded"
          >
            Create Room
          </button>
        </div>
      </div>

      {/* Study Rooms List */}
      <div>
        <h3 className="text-xl font-semibold mb-4">Available Study Rooms</h3>
        <div className="grid md:grid-cols-3 gap-4">
          {studyRooms.map(room => (
            <div 
              key={room.id} 
              className="border p-4 rounded-lg shadow-md"
            >
              <h4 className="font-bold text-lg">{room.name}</h4>
              <p>Subject: {room.subject}</p>
              <p>Class: {room.class}</p>
              <p>Created by: {room.creatorName}</p>
              <button 
                className="mt-2 bg-green-500 text-white px-4 py-2 rounded"
                onClick={() => {/* Join room logic */}}
              >
                Join Room
              </button>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default StudyRooms;