import React, { useState } from 'react';
import { createUserWithEmailAndPassword, sendEmailVerification } from 'firebase/auth';
import { auth, db, query, collection, where, getDocs } from './firebase';
import { doc, setDoc } from 'firebase/firestore';
import { useNavigate } from 'react-router-dom';

const Register = () => {
  const [name, setName] = useState('');
  const [username, setUsername] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [studentClass, setStudentClass] = useState('');
  const [age, setAge] = useState('');
  const navigate = useNavigate();

  const handleRegister = async () => {
    try {
      // Check if username already exists
      const usernameQuery = query(
        collection(db, 'users'), 
        where('username', '==', username)
      );
      const usernameSnapshot = await getDocs(usernameQuery);
      
      if (!usernameSnapshot.empty) {
        alert('Username already exists');
        return;
      }

      const userCredential = await createUserWithEmailAndPassword(auth, email, password);
      const user = userCredential.user;
      
      await sendEmailVerification(user);
      
      // Store additional user info
      await setDoc(doc(db, 'users', user.uid), {
        name,
        username,
        email,
        class: studentClass,
        age,
        uid: user.uid,
        friends: [], // Initialize empty friends array
        friendRequests: [], // Initialize empty friend requests array
        profilePicture: '', // Optional: for future profile picture feature
        bio: '' // Optional: for user bio
      });

      alert("Registration successful! Please verify your email.");
      navigate('/verify-email');
    } catch (error) {
      alert(error.message);
    }
  };

  return (
    <div style={{ textAlign: 'center', marginTop: '50px' }}>
      <h2>Register</h2>
      <input placeholder="Name" value={name} onChange={(e) => setName(e.target.value)} /><br />
      <input placeholder="Username" value={username} onChange={(e) => setUsername(e.target.value)} /><br />
      <input placeholder="Email" value={email} onChange={(e) => setEmail(e.target.value)} /><br />
      <input placeholder="Password" type="password" value={password} onChange={(e) => setPassword(e.target.value)} /><br />
      <input placeholder="Class (9 or 10)" value={studentClass} onChange={(e) => setStudentClass(e.target.value)} /><br />
      <input placeholder="Age" value={age} onChange={(e) => setAge(e.target.value)} /><br />
      <button onClick={handleRegister}>Register</button>
      <br />
      <a href="/Login">Login</a>
    </div>
  );
};

export default Register;