import React, { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { 
  db, 
  auth, 
  doc, 
  getDoc, 
  collection, 
  addDoc, 
  query, 
  where, 
  getDocs,
  updateDoc,
  orderBy
} from './firebase';
import { formatDistance } from 'date-fns';

const QuestionDetail = () => {
  const { questionId } = useParams();
  const [question, setQuestion] = useState(null);
  const [replies, setReplies] = useState([]);
  const [newReply, setNewReply] = useState('');
  const navigate = useNavigate();

  useEffect(() => {
    const fetchQuestionAndReplies = async () => {
      try {
        // Fetch question details
        const questionDoc = await getDoc(doc(db, 'community_questions', questionId));
        if (questionDoc.exists()) {
          setQuestion({ id: questionDoc.id, ...questionDoc.data() });
        }

        // Fetch replies
        const repliesQuery = query(
          collection(db, 'community_questions', questionId, 'replies'),
          orderBy('timestamp', 'desc')
        );
        const repliesSnapshot = await getDocs(repliesQuery);
        const fetchedReplies = repliesSnapshot.docs.map(doc => ({
          id: doc.id,
          ...doc.data()
        }));
        setReplies(fetchedReplies);
      } catch (error) {
        console.error("Error fetching question details:", error);
      }
    };

    fetchQuestionAndReplies();
  }, [questionId]);

  const handleReplySubmit = async (e) => {
    e.preventDefault();
    const user = auth.currentUser;

    if (!newReply.trim()) return;

    try {
      // Add reply to Firestore
      const replyRef = await addDoc(
        collection(db, 'community_questions', questionId, 'replies'), 
        {
          text: newReply,
          author: user.uid,
          authorName: user.displayName || user.email.split('@')[0],
          timestamp: new Date(),
          votes: 0,
          isCorrect: false
        }
      );

      // Update question's reply count
      await updateDoc(doc(db, 'community_questions', questionId), {
        replyCount: (question.replyCount || 0) + 1
      });

      // Update local state
      setReplies([
        {
          id: replyRef.id,
          text: newReply,
          author: user.uid,
          authorName: user.displayName || user.email.split('@')[0],
          timestamp: new Date(),
          votes: 0,
          isCorrect: false
        },
        ...replies
      ]);

      // Clear reply input
      setNewReply('');
    } catch (error) {
      console.error("Error submitting reply:", error);
    }
  };

  const handleVote = async (replyId, isUpvote) => {
    // Implement voting logic
    try {
      const replyRef = doc(db, 'community_questions', questionId, 'replies', replyId);
      const replyDoc = await getDoc(replyRef);
      const currentVotes = replyDoc.data().votes || 0;

      await updateDoc(replyRef, {
        votes: isUpvote ? currentVotes + 1 : currentVotes - 1
      });

      // Update local state
      setReplies(replies.map(reply => 
        reply.id === replyId 
          ? {...reply, votes: isUpvote ? currentVotes + 1 : currentVotes - 1}
          : reply
      ));
    } catch (error) {
      console.error("Error voting:", error);
    }
  };

  if (!question) {
    return <div>Loading...</div>;
  }

  return (
    <div className="container mx-auto p-4">
      <button 
        onClick={() => navigate('/community')}
        className="mb-4 text-blue-500 hover:underline"
      >
         Back to Community
      </button>

      <div className="bg-white shadow-md rounded-lg p-6 mb-6">
        <div className="flex justify-between items-center mb-4">
          <div>
            <h2 className="text-xl font-bold">{question.authorName}</h2>
            <p className="text-gray-500 text-sm">
              {formatDistance(question.timestamp.toDate(), new Date(), { addSuffix: true })}
            </p>
          </div>
          <span className="text-sm text-gray-600">
            Subject: {question.subject}
          </span>
        </div>

        <p className="mb-4">{question.text}</p>

        {question.tags && question.tags.length > 0 && (
          <div className="flex space-x-2 mb-4">
            {question.tags.map(tag => (
              <span 
                key={tag} 
                className="bg-blue-100 text-blue-800 px-2 py-1 rounded-full text-xs"
              >
                {tag}
              </span>
            ))}
          </div>
        )}
      </div>

      {/* Reply Input */}
      <form onSubmit={handleReplySubmit} className="mb-6">
        <div className="flex">
          <textarea 
            value={newReply}
            onChange={(e) => setNewReply(e.target.value)}
            placeholder="Write your reply..."
            className="flex-grow border p-2 rounded-l h-24"
            required
          />
          <button 
            type="submit"
            className="bg-blue-500 text-white px-4 py-2 rounded-r"
          >
            Post Reply
          </button>
        </div>
      </form>

      {/* Replies Section */}
      <div>
        <h3 className="text-lg font-bold mb-4">
          Replies ({replies.length})
        </h3>

        {replies.length === 0 ? (
          <div className="text-center text-gray-500">
            No replies yet. Be the first to respond!
          </div>
        ) : (
          replies.map(reply => (
            <div 
              key={reply.id} 
              className="border p-4 mb-4 rounded-lg shadow-sm"
            >
              <div className="flex justify-between items-center mb-2">
                <div>
                  <h4 className="font-semibold">{reply.authorName}</h4>
                  <p className="text-gray-500 text-sm">
                    {formatDistance(reply.timestamp.toDate(), new Date(), { addSuffix: true })}
                  </p>
                </div>
                <div className="flex items-center space-x-2">
                  <button 
                    onClick={() => handleVote(reply.id, true)}
                    className="flex items-center"
                  >
                     {reply.votes}
                  </button>
                  {reply.isCorrect && (
                    <span className="text-green-500"> Correct Answer</span>
                  )}
                </div>
              </div>
              <p>{reply.text}</p>
            </div>
          ))
        )}
      </div>
    </div>
  );
};

export default QuestionDetail;