import React, { useState, useEffect } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { 
  db, 
  auth, 
  collection, 
  query, 
  where, 
  getDocs,
  addDoc
} from './firebase';
import ChatComponent from './ChatComponent';

const ChatPage = () => {
  const [friends, setFriends] = useState([]);
  const [selectedFriend, setSelectedFriend] = useState(null);
  const location = useLocation();
  const navigate = useNavigate();

  useEffect(() => {
    // Check if a friend was pre-selected from friend profile
    if (location.state?.selectedFriend) {
      setSelectedFriend(location.state.selectedFriend);
    }
  }, [location.state]);

  useEffect(() => {
    const fetchFriends = async () => {
      const user = auth.currentUser;
      if (user) {
        try {
          // Fetch user's friends
          const friendsQuery = query(
            collection(db, 'users', user.uid, 'friends')
          );
          
          const friendsSnapshot = await getDocs(friendsQuery);
          const fetchedFriends = friendsSnapshot.docs.map(doc => ({
            id: doc.data().userId,
            ...doc.data()
          }));
          
          setFriends(fetchedFriends);
        } catch (error) {
          console.error("Error fetching friends:", error);
        }
      }
    };

    fetchFriends();
  }, []);

  const handleSelectFriend = (friend) => {
    setSelectedFriend(friend);
  };

  return (
    <div className="container mx-auto p-4 flex">
      {/* Friends List */}
      <div className="w-1/3 pr-4 border-r">
        <h1 className="text-2xl font-bold mb-6">Friends</h1>
        
        {friends.length === 0 ? (
          <div className="text-center text-gray-500">
            No friends to chat with. Add friends first!
          </div>
        ) : (
          <div className="space-y-2">
            {friends.map(friend => (
              <div 
                key={friend.id} 
                onClick={() => handleSelectFriend(friend)}
                className={`p-3 border rounded cursor-pointer hover:bg-gray-100 ${
                  selectedFriend?.id === friend.id ? 'bg-blue-100' : ''
                }`}
              >
                <h3 className="font-bold">{friend.name}</h3>
                <p className="text-gray-500">@{friend.username}</p>
              </div>
            ))}
          </div>
        )}
      </div>

      {/* Chat Area */}
      <div className="w-2/3 pl-4">
        {selectedFriend ? (
          <ChatComponent friend={selectedFriend} />
        ) : (
          <div className="text-center text-gray-500">
            Select a friend to start chatting
          </div>
        )}
      </div>
    </div>
  );
};

export default ChatPage;