import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { 
  db, 
  auth, 
  collection, 
  getDocs, 
  query, 
  where, 
  getDoc, 
  doc,
  orderBy
} from './firebase';
import { formatDistance } from 'date-fns';

const Community = () => {
  const [questions, setQuestions] = useState([]);
  const [filters, setFilters] = useState({
    class: '',
    subject: '',
    sortBy: 'recent'
  });
  const [loading, setLoading] = useState(true);
  const navigate = useNavigate();

  useEffect(() => {
    const fetchQuestions = async () => {
      const user = auth.currentUser;
      if (user) {
        try {
          const userDoc = await getDoc(doc(db, 'users', user.uid));
          const userData = userDoc.data();
          
          let q = query(
            collection(db, 'community_questions'),
            where('class', '==', userData.class)
          );

          // Add subject filter if selected
          if (filters.subject) {
            q = query(q, where('subject', '==', filters.subject));
          }

          // Sort questions
          if (filters.sortBy === 'recent') {
            q = query(q, orderBy('timestamp', 'desc'));
          } else if (filters.sortBy === 'most_replied') {
            q = query(q, orderBy('replyCount', 'desc'));
          }

          const querySnapshot = await getDocs(q);
          const fetchedQuestions = querySnapshot.docs.map(doc => ({
            id: doc.id,
            ...doc.data()
          }));
          
          setQuestions(fetchedQuestions);
          setLoading(false);
        } catch (error) {
          console.error("Error fetching questions:", error);
          setLoading(false);
        }
      }
    };

    fetchQuestions();
  }, [filters]);

  const renderQuestionCard = (question) => (
    <div key={question.id} className="border p-4 mb-4 rounded-lg shadow-md">
      <div className="flex justify-between items-center mb-2">
        <div>
          <h3 className="font-bold">{question.authorName}</h3>
          <p className="text-gray-500 text-sm">
            {formatDistance(question.timestamp.toDate(), new Date(), { addSuffix: true })}
          </p>
        </div>
      </div>
      <p className="mb-2">{question.text}</p>
      <div className="flex justify-between items-center">
        <span className="text-sm text-gray-600">
          Subject: {question.subject || 'N/A'} | 
          Replies: {question.replyCount || 0}
        </span>
        <button 
          onClick={() => navigate(`/question/${question.id}`)}
          className="text-blue-500 hover:underline"
        >
          View Details
        </button>
      </div>
    </div>
  );

  return (
    <div className="container mx-auto p-4">
      <div className="flex justify-between items-center mb-6">
        <h1 className="text-2xl font-bold">Community Questions</h1>
        <button 
          onClick={() => navigate('/ask-question')}
          className="bg-blue-500 text-white px-4 py-2 rounded"
        >
          Ask Question
        </button>
      </div>

      {/* Filters */}
      <div className="mb-6 flex space-x-4">
        <select 
          value={filters.subject} 
          onChange={(e) => setFilters({...filters, subject: e.target.value})}
          className="border p-2 rounded"
        >
          <option value="">All Subjects</option>
          <option value="Math">Math</option>
          <option value="Science">Science</option>
          <option value="History">History</option>
          <option value="English">English</option>
        </select>

        <select 
          value={filters.sortBy}
          onChange={(e) => setFilters({...filters, sortBy: e.target.value})}
          className="border p-2 rounded"
        >
          <option value="recent">Most Recent</option>
          <option value="most_replied">Most Replied</option>
        </select>
      </div>

      {loading ? (
        <div>Loading questions...</div>
      ) : questions.length > 0 ? (
        questions.map(renderQuestionCard)
      ) : (
        <div className="text-center text-gray-500">
          No questions found. Be the first to ask!
        </div>
      )}
    </div>
  );
};

export default Community;