import React, { useState, useEffect, useRef, useCallback } from 'react';
import io from 'socket.io-client';
import { 
  db, 
  auth, 
  collection, 
  query, 
  orderBy, 
  getDocs,
} from './firebase';

const ChatComponent = ({ friend }) => {
  const [socket, setSocket] = useState(null);
  const [messages, setMessages] = useState([]);
  const [newMessage, setNewMessage] = useState('');
  const [imagePreview, setImagePreview] = useState(null);
  const messagesEndRef = useRef(null);
  const fileInputRef = useRef(null);
  const currentUser = auth.currentUser;

  // Generate a unique chat room ID
  const getChatRoomId = useCallback(() => {
    return [currentUser.uid, friend.id].sort().join('_');
  }, [currentUser.uid, friend.id]);

  // Socket and Message Setup
  useEffect(() => {
    // Connect to Socket.IO server
    const newSocket = io('http://localhost:5000');
    setSocket(newSocket);

    // Register user
    newSocket.emit('register_user', currentUser.uid);

    // Fetch previous messages
    const fetchMessages = async () => {
      const chatRoomId = getChatRoomId();
      
      const messagesQuery = query(
        collection(db, 'private_messages', chatRoomId, 'messages'),
        orderBy('timestamp', 'asc')
      );

      try {
        const querySnapshot = await getDocs(messagesQuery);
        const fetchedMessages = querySnapshot.docs.map(doc => ({
          id: doc.id,
          ...doc.data()
        }));
        setMessages(fetchedMessages);
      } catch (error) {
        console.error("Error fetching messages:", error);
      }
    };

    fetchMessages();

    // Listen for real-time messages
    newSocket.on('receive_private_message', (message) => {
      setMessages(prevMessages => [...prevMessages, message]);
    });

    // Cleanup on unmount
    return () => {
      newSocket.disconnect();
    };
  }, [friend.id, currentUser.uid, getChatRoomId]);

  // Scroll to bottom when messages change
  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  }, [messages]);

  // Image file handling
  const handleImageSelect = (e) => {
    const file = e.target.files[0];
    if (file) {
      const reader = new FileReader();
      reader.onloadend = () => {
        setImagePreview(reader.result);
      };
      reader.readAsDataURL(file);
    }
  };

  // Send message with optional image
  const handleSendMessage = async (e) => {
    e.preventDefault();
    
    // If there's an image, upload it first
    if (imagePreview) {
      socket.emit('upload_image', imagePreview, (response) => {
        if (response.success) {
          sendMessageWithImage(response.imageUrl);
        } else {
          alert('Image upload failed');
        }
      });
    } else {
      sendTextMessage();
    }
  };

  // Send text message
  const sendTextMessage = () => {
    if (!newMessage.trim()) return;

    const messageData = {
      senderId: currentUser.uid,
      senderName: currentUser.displayName || currentUser.email.split('@')[0],
      receiverId: friend.id,
      text: newMessage,
      timestamp: new Date()
    };

    // Emit message via socket
    socket.emit('send_private_message', messageData);

    // Clear input
    setNewMessage('');
  };

  // Send message with image
  const sendMessageWithImage = (imageUrl) => {
    const messageData = {
      senderId: currentUser.uid,
      senderName: currentUser.displayName || currentUser.email.split('@')[0],
      receiverId: friend.id,
      text: newMessage,
      imageUrl: imageUrl,
      timestamp: new Date()
    };

    // Emit message via socket
    socket.emit('send_private_message', messageData);

    // Clear inputs
    setNewMessage('');
    setImagePreview(null);
    fileInputRef.current.value = '';
  };

  // Remove image preview
  const clearImagePreview = () => {
    setImagePreview(null);
    fileInputRef.current.value = '';
  };

  return (
    <div className="chat-container bg-white border rounded-lg shadow-md flex flex-col h-[600px]">
      {/* Chat Header */}
      <div className="chat-header p-4 bg-blue-500 text-white rounded-t-lg">
        <h2>Chat with {friend.name}</h2>
      </div>

      {/* Messages Container */}
      <div className="flex-grow overflow-y-auto p-4">
        {messages.map((msg, index) => (
          <div 
            key={index}
            className={`mb-2 flex ${
              msg.senderId === currentUser.uid 
                ? 'justify-end' 
                : 'justify-start'
            }`}
          >
            <div 
              className={`p-2 rounded-lg max-w-xs ${
                msg.senderId === currentUser.uid 
                  ? 'bg-blue-100 text-right' 
                  : 'bg-gray-100'
              }`}
            >
              {msg.text && <p>{msg.text}</p>}
              {msg.imageUrl && (
                <img 
                  src={msg.imageUrl} 
                  alt="Shared" 
                  className="max-w-full rounded-lg mt-2"
                />
              )}
              <small className="text-xs text-gray-500">
                {msg.senderName}
              </small>
            </div>
          </div>
        ))}
        <div ref={messagesEndRef} />
      </div>

      {/* Image Preview */}
      {imagePreview && (
        <div className="p-2 bg-gray-100 flex items-center">
          <img 
            src={imagePreview} 
            alt="Preview" 
            className="h-20 w-20 object-cover rounded mr-2"
          />
          <button 
            onClick={clearImagePreview}
            className="text-red-500"
          >
            Remove
          </button>
        </div>
      )}

      {/* Message Input */}
      <form 
        onSubmit={handleSendMessage}
        className="p-4 border-t flex items-center"
      >
        <input 
          type="file"
          ref={fileInputRef}
          accept="image/*"
          onChange={handleImageSelect}
          className="hidden"
          id="image-upload"
        />
        <label 
          htmlFor="image-upload" 
          className="mr-2 cursor-pointer"
        >
          
        </label>
        <input 
          type="text"
          value={newMessage}
          onChange={(e) => setNewMessage(e.target.value)}
          placeholder="Type a message..."
          className="flex-grow p-2 border rounded-l"
        />
        <button 
          type="submit"
          className="bg-blue-500 text-white px-4 py-2 rounded-r"
        >
          Send
        </button>
      </form>
    </div>
  );
};

export default ChatComponent;